.footer {
  background-color: #2C2C2C;
  display: grid;
  grid-template-rows: 1fr 70px;

  & .footer--bar {
    display: flex;
    box-sizing: border-box;
    padding-top: 58px;
    justify-content: space-between;

    & .text-header {
      margin-bottom: 25px;
    } 
  }

  & .footer-menu {
    margin-left: 99px;

    & a {
      color: white;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
    }

    .menu {
      padding: 0;
      & ul {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        & li {
          margin-bottom: 16px;
        }
      }
    } 
  }

  & .contact-wrapper {
    margin-left: 157px;

    & .text-with-logo {
      display: flex;
      margin-bottom: 32px;
      align-items: center;
    }
  }

  & .social-wrapper {
    margin-right: 212px;

    & .social-panel {
      display: flex;
      justify-content: space-between;

      .icon:hover {
        animation: iconactive 0.4s ease-in-out infinite;
      }
    }
  }

  .footer--copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255,255,255,0.1);
    line-height: 22px;
    font-size: 12px;

    & .footer--copyright__text {
      color:rgba(200, 200, 200, 0.5);
    }
  }
}

